import * as React from "react"
// import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import mediumZoom from "gatsby-remark-images-medium-zoom";

import Layout from "../components/layout/layout"
import SEO from "../components/seo"
import * as subpageStyles from "./modules/subpage.module.css"
import * as styles from "./works/works.module.css"

const WorkPage = () => (
    <Layout>
        <SEO title="Works" />
        <section className={subpageStyles.wrapper}>
            <h1>Works</h1>
            <ul className={subpageStyles.contentlist}>
                <li>
                    <a href="#webapps">Web Applications</a>
                </li>
                <li>
                    <a href="#dtapps">Desktop Applications</a>
                </li>
                <li>
                    <a href="#designs">Designs</a>
                </li>
                <li>
                    <a href="#others">Others</a>
                </li>
            </ul>
            <article id="webapps">
                <h2>Web Applications</h2>
                <section>
                    <h3>Oshapport</h3>
                    <date>2020.9 -</date>
                    <ul className={styles.skill_list}>
                        <li>
                            Django
                        </li>
                        <li>
                            Python
                        </li>
                        <li>
                            PostgreSQL
                        </li>
                        <li>
                            HTML
                        </li>
                        <li>
                            CSS(SCSS)
                        </li>
                    </ul>
                    <p>
                        「おしゃれ」＋「港(port)」というテーマで、ファッション関連の情報をを扱うQ&amp;Aサイトを制作しました。
                        
                    </p>
                    <StaticImage
                        src="..\images\oshapport\oshapport_qanda.png"
                        width={500}
                        quality={100}
                        formats={["AUTO", "WEBP", "AVIF"]}
                        alt="A Gatsby astronaut"
                    />
                </section>
            </article>
            <article id="dtapps">
                <h2>Desktop Applications</h2>
                <section>
                    <h3>task-management-app</h3>
                    <date>2020.12 - 2021.3</date>
                    <ul className={styles.skill_list}>
                        <li>
                            Electron
                        </li>
                        <li>
                            NeDB
                        </li>
                        <li>
                            JavaScript
                        </li>
                        <li>
                            Node.js
                        </li>
                        <li>
                            HTML
                        </li>
                        <li>
                            CSS(SCSS)
                        </li>
                    </ul>
                    <p>
                        タスク管理アプリです。
                    </p>
                    <div className={styles.img_wrapper}>
                        <StaticImage
                            src="..\images\task-management-app\nedb_practice-02.png"
                            width={500}
                            quality={100}
                            formats={["AUTO", "WEBP", "AVIF"]}
                            alt="A Gatsby astronaut"
                        />
                        <StaticImage
                            src="..\images\task-management-app\nedb_practice-04.png"
                            width={500}
                            quality={100}
                            formats={["AUTO", "WEBP", "AVIF"]}
                            alt="A Gatsby astronaut"
                        />
                        <StaticImage
                            src="..\images\task-management-app\nedb_practice-05.png"
                            width={500}
                            quality={100}
                            formats={["AUTO", "WEBP", "AVIF"]}
                            alt="A Gatsby astronaut"
                        />
                        <StaticImage
                            src="..\images\task-management-app\nedb_practice-06.png"
                            width={500}
                            quality={100}
                            formats={["AUTO", "WEBP", "AVIF"]}
                            alt="A Gatsby astronaut"
                        />
                    </div>
                </section>
            </article>
            <article id="designs">
                <h2>Designs</h2>
            </article>
            <article id="others">
                <h2>Others</h2>
            </article>
        </section>
        {/* <StaticImage
      src="../images/gatsby-astronaut.png"
      width={300}
      quality={95}
      formats={["AUTO", "WEBP", "AVIF"]}
      alt="A Gatsby astronaut"
    /> */}
    </Layout>
)

export default WorkPage
